// --- FONT SIZES FOR H1 HEADLINES ---


// <1920
$h1-dl: 4.64vw;
$h1-dl-fall: 78px;
$h1-dl-min: 78px;
$h1-dl-max: 88px;

// <1680
$h1-dm: 4.68vw;
$h1-dm-fall: 60px;
$h1-dm-min: 60px;
$h1-dm-max: 78px;

// <1280
$h1-ds: 5.08vw;
$h1-ds-fall: 52px;
$h1-ds-min: 52px;
$h1-ds-max: 60px;

// <1024
// $h1-tm: 88px

// <992
$h1-tm: 8.88vw;
$h1-tm-fall: 58px;
$h1-tm-min: 58px;
$h1-tm-max: 88px;

// <720
$h1-ts: 8.05vw;
$h1-ts-fall: 48px;
$h1-ts-min: 48px;
$h1-ts-max: 58px;

// <=630 (special breakpoint when font-size propoptional decreasing became fixed)
$h1-ml: 48px;

// <375
$h1-mm: 12vw;
$h1-mm-fall: 38px;
$h1-mm-min: 38px;
$h1-mm-max: 48px;

$edgeValue: 5;

@mixin h1-style {
    @include hm-sb;
    font-size: 88px;
    line-height: 0.96;
    text-decoration: underline;
    @include desktop-less-xl {
        font-size: $h1-dl-fall;
        font-size: $h1-dl;
 }        // +responsive-font($h1-dl, $h1-dl-min, $h1-dl-max, $h1-dl-fall)
    @include desktop-less-lg {
        font-size: $h1-dm-fall;
        font-size: $h1-dm;
 }        // +responsive-font($h1-dm, $h1-dm-min, $h1-dm-max, $h1-dm-fall)
    @include desktop-less-md {
        font-size: $h1-ds-fall;
        font-size: $h1-ds;
 }        // +responsive-font($h1-ds, $h1-ds-min, $h1-ds-max, $h1-ds-fall)
    @include r(991) {
        font-size: $h1-tm-fall;
        font-size: $h1-tm;
 }        // +responsive-font($h1-tm, $h1-tm-min, $h1-tm-max, $h1-tm-fall)
    @include rorient(719, portrait) {
        font-size: $h1-ts-fall;
        font-size: $h1-ts;
 }        // +responsive-font($h1-ts, $h1-ts-min, $h1-ts-max, $h1-ts-fall)
    @include rorient(719, landscape) {
        font-size: $h1-ts; }
    @include rorient(630, portrait) {
        line-height: 0.93;
        font-size: $h1-ml; }
    @include rorient(630, landscape) {
        font-size: $h1-ml; }
    @include rorient(374, portrait) {
        font-size: $h1-mm-fall;
        font-size: $h1-mm; }
    @include rorient(374, landscape) {
        font-size: $h1-mm; }

    // +rorient(1023, portrait)
    //     font-size: $h1-tm
    // +rorient(1023, landscape)
    //     font-size: 52px
    // +rorient(719, portrait)
    //     +responsive-font($h1-ml, $h1-ml-min, $h1-ml-max, $h1-ml-fall)
    // +rorient(719, landscape)
    //     font-size: 52px
    // +rorient(599, portrait)
    //     line-height: 0.93
    //     +responsive-font($h1-mm, $h1-mm-min, $h1-mm-max, $h1-mm-fall)
    // +rorient(599, landscape)
    //     font-size: 38px
    // +rorient(374, portrait)
    //     line-height: 0.92
 }    //     +responsive-font($h1-ms, $h1-ms-min, $h1-ms-max, $h1-ms-fall)



// --- FONT SIZES FOR H2 HEADLINES ---
h1 {
    @include h1-style; }
// <1920
$h2-dl: 62px;

// <1680
$h2-dm: 3.435vw;
$h2-dm-fall: 44px;
$h2-dm-min: 44px;
$h2-dm-max: 62px;

// <1280
$h2-ds: 3.125vw;
$h2-ds-fall: 32px;
$h2-ds-min: 32px;
$h2-ds-max: 44px;

// <992
$h2-tm: 4.44vw;
$h2-tm-fall: 32px;
$h2-tm-min: 32px;
$h2-tm-max: 44px;

// <720
$h2-ts: 4.44vw;
$h2-ts-fall: 28px;
$h2-ts-min: 28px;
$h2-ts-max: 32px;

// <=630 (special breakpoint when font-size propoptional decreasing became fixed)
$h2-ml: 28px;


h2 {
    @include hm-sb;
    line-height: 0.93;
    font-size: $h2-dl;
    text-decoration: underline;
    @include desktop-less-xl {
 }        // line-height: 0.9
    @include desktop-less-lg {
        // line-height: 0.92
        font-size: $h2-dm-fall;
        font-size: $h2-dm;
 }        // +responsive-font($h2-dm, $h2-dm-min, $h2-dm-max, $h2-dm-fall)
    @include desktop-less-md {
        // line-height: 0.94
        font-size: $h2-ds-fall;
        font-size: $h2-ds;
 }        // +responsive-font($h2-ds, $h2-ds-min, $h2-ds-max, $h2-ds-fall)
    @include r(991) {
        font-size: $h2-tm-fall;
        font-size: $h2-tm;
 }        // +responsive-font($h2-tm, $h2-tm-min, $h2-tm-max, $h2-tm-fall)
    @include tablet-less-sm {
        font-size: $h2-ts-fall;
        font-size: $h2-ts; }
    @include r(630) {
        line-height: 0.95;
        font-size: $h2-ml; } }


